import moment from 'moment';
import 'moment/locale/ru';

//       
export default function getDatesInMonthDisplay(date) {  
  moment.locale('ru');

  const nowDate = moment(date);
  const countDaysInMonth = nowDate.daysInMonth();                   //     
  const firstWeekday = nowDate.startOf('month').isoWeekday();       //     

  const result = [];
  
  //    
  if (firstWeekday !== 1) {
    const lastMonth = nowDate.clone().subtract(1, 'month');   //  
    const lastMonthDays = lastMonth.daysInMonth();            //     

    for (let j = firstWeekday - 1; j > 0; j--) {  
      result.push({  
        date: moment(lastMonth).add(lastMonthDays - j, 'days'),  
        currentMonth: false  
      })  
    }
  }

  //    
  for (let day = 1; day <= countDaysInMonth; day++) {
    result.push({
      date: moment(nowDate).date(day), 
      currentMonth: true
    })
  }

  //    
  const nextMonth = nowDate.clone().add(1, 'month');        //  
  const remainingDays = 7 - (result.length % 7);            //        
  if (remainingDays !== 7) {
    for (let day = 1; day <= remainingDays; day++) {
      result.push({
        date: moment(nextMonth).date(day), 
        currentMonth: false
      })
    }
  }
  return result;  
}